<template>
    <div id="view-member">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">House: {{house}}</li>
            <li class="collection-item">Points: {{points}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteMember" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-member', params:{member_id: member_id}}" class="btn-floating btn-large red">
                <i class="fa fa-edit"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-member',
    data () {
        return {
            member_id: null,
            name: null,
            house: null,
            house_id: 0,
            points: 0
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('members').where('member_id', '==', to.params.member_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.member_id = doc.data().member_id
                    vm.name = doc.data().name
                    vm.house = doc.data().house
                    vm.house_id = doc.data().house_id
                    vm.points = doc.data().points
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            console.log('polpo')
            db.collection('members').where('member_id', '==', this.$route.params.member_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.member_id = doc.data().member_id
                    this.name = doc.data().name
                    this.house = doc.data().house
                    this.house_id = doc.data().house_id
                    this.points = doc.data().points
                })
            })
        },
        deleteMember () {
            if (confirm('Are you sure?')) {
                db.collection('members').where('member_id', '==', this.$route.params.member_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>


