<template>
    <div id="edit-member">
        <h3>Edit member</h3>
        <div class="row">
            <form @submit.prevent="updateMember" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" disabled v-model="member_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="house" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="house_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="points" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link v-bind:to="{name: 'view-member', params: {member_id: member_id}}" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-member',
    data () {
        return {
            member_id: null,
            name: null,
            house: null,
            house_id: null,
            points: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('members').where('member_id', '==', to.params.member_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.member_id = doc.data().member_id
                    vm.name = doc.data().name
                    vm.house = doc.data().house
                    vm.house_id = doc.data().house_id
                    vm.points = doc.data().points
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            console.log('polpo')
            db.collection('members').where('member_id', '==', this.$route.params.member_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.member_id = doc.data().member_id
                    this.name = doc.data().name
                    this.house = doc.data().house
                    this.house_id = doc.data().house_id
                    this.points = doc.data().points
                })
            })
        },
        updateMember () {
            console.log('polpo')
            db.collection('members').where('member_id', '==', this.$route.params.member_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        member_id: this.member_id,
                        name: this.name,
                        house: this.house,
                        house_id: this.house_id,
                        points: this.points
                    })
                    .then(() => {
                        this.$router.push({name: 'view-member', params:{member_id: this.member_id}})
                    })
                })
            })
        }
    }
}
</script>


