<template>
    <div id="new-member">
        <h3>New member</h3>
        <div class="row">
            <form @submit.prevent="saveMember" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="member_id" required>
                        <label>Member ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="house" required>
                        <label>House</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="house_id" required>
                        <label>House ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="points" required>
                        <label>Points</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-member',
    data () {
        return {
            member_id: null,
            name: null,
            house: null,
            house_id: null,
            points: null
        }
    },
    methods: {
        saveMember () {
            db.collection('members').add({
                member_id: this.member_id,
                name: this.name,
                house: this.house,
                house_id: this.house_id,
                points: this.points
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>


